import clsx from "clsx"
import {Children, isValidElement, useState} from "react"
import DotsSelect from "~/components/UI/DotsSelect/DotsSelect"
import Fader from "~/components/UI/Fader/Fader"
import MultiStyleText from "~/components/UI/MultiStyleText/MultiStyleText"
import RichTextField from "~/components/UI/RichText/RichText"
import Tabs from "~/components/UI/Tabs/Tabs"
import "./Careers.css"

interface CareersProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title"> {
    title: string | null | undefined
    description: string | null | undefined
    lookingFor: string | null | undefined
}

interface CareersComponents {
    OpenPosition: React.FC<CareersOpenPositionProps>
}

const Careers: React.FC<CareersProps> & CareersComponents = props => {
    const {className, title, description, lookingFor, children, ...otherProps} = props
    const [selectedPosition, setSelectedPosition] = useState<number>(0)

    const openPositions: Array<React.ReactElement<CareersOpenPositionProps>> = []
    const rest: Array<React.ReactNode> = []
    Children.forEach(children, child => {
        if (isValidElement<CareersOpenPositionProps>(child) && child.type === CareersOpenPosition) {
            openPositions.push(child)
        } else {
            rest.push(child)
        }
    })

    return (
        <div className={clsx("careers-853t", className)} {...otherProps}>
            <div className="intro-853t">
                <h2 className="title-853t">
                    <MultiStyleText className1="title-part-1-853t" className2="title-part-2-853t">
                        {title}
                    </MultiStyleText>
                </h2>
                <RichTextField className="chars-h6-80">{description}</RichTextField>
            </div>
            {openPositions.length > 0 && (
                <div className="open-positions-853t">
                    <h5 className="looking-for-853t">{lookingFor}:</h5>
                    <div className="tabs-container-853t">
                        <Tabs
                            selected={selectedPosition}
                            onSelect={setSelectedPosition}
                            className="tabs-853t"
                            tabClass="tab-853t"
                            tabActiveClass="tab-active-853t"
                        >
                            {openPositions.map((position, index) => (
                                <Tabs.Tab key={index}>{position.props.name}</Tabs.Tab>
                            ))}
                        </Tabs>
                        <span className="mobile-lf-title-853t">{openPositions[selectedPosition]?.props.name}</span>
                    </div>
                    <Fader selected={selectedPosition} className="description-853t" onSelect={setSelectedPosition}>
                        {openPositions.map((position, index) => (
                            <Fader.Slide key={index}>
                                <RichTextField>{position.props.description}</RichTextField>
                            </Fader.Slide>
                        ))}
                    </Fader>
                    <DotsSelect
                        length={openPositions.length}
                        selected={selectedPosition}
                        onSelect={setSelectedPosition}
                        className="dots-select-853t"
                    />
                </div>
            )}
            <div className="children-853t">{rest}</div>
        </div>
    )
}

export default Careers

interface CareersOpenPositionProps {
    name: string | null | undefined
    description: string | null | undefined
}

const CareersOpenPosition: React.FC<CareersOpenPositionProps> = props => null

Careers.OpenPosition = CareersOpenPosition
