import clsx from "clsx"
import {GatsbyImage, IGatsbyImageData} from "gatsby-plugin-image"
import {Children, isValidElement} from "react"
import "./Timeline.css"

interface TimelineProps extends React.HTMLAttributes<HTMLDivElement> {
    backgroundImage?: IGatsbyImageData | undefined | null
    alt?: string | undefined | null
}

interface TimelineComponents {
    Event: React.FC<TimelineEventProps>
}

const Timeline: React.FC<TimelineProps> & TimelineComponents = props => {
    const {className, backgroundImage, alt, children, ...otherProps} = props

    const events: Array<React.ReactElement<TimelineEventProps>> = []
    Children.forEach(children, child => {
        if (isValidElement<TimelineEventProps>(child)) {
            events.push(child)
        }
    })

    const scrollIntoView = (event: React.MouseEvent<HTMLButtonElement>) => {
        event.currentTarget.scrollIntoView({
            behavior: "smooth",
            block: "nearest",
            inline: "center",
        })
    }

    return (
        <div className={clsx("timeline-rhh9", className)} {...otherProps}>
            {backgroundImage && alt && (
                <GatsbyImage className="background-image-rhh9" alt={alt} image={backgroundImage} />
            )}
            <div className="scroller-rhh9">
                {events.map((timelineEvent, index) => {
                    return (
                        <button className="event-rhh9" key={index} type="button" onClick={scrollIntoView}>
                            <div className="ball-1-rhh9">
                                <div className="ball-2-rhh9">
                                    <div className="ball-3-rhh9">
                                        {timelineEvent.props.icon && <img src={timelineEvent.props.icon} />}
                                    </div>
                                </div>
                                <div className="lip-rhh9" />
                            </div>
                            <h5 className="date-rhh9">{timelineEvent.props.title}</h5>
                            <div className="flow-rhh9">
                                <div className="line-pre-rhh9" />
                                <div className="dot-1-rhh9">
                                    <div className="dot-2-rhh9" />
                                </div>
                                <div className="line-post-rhh9" />
                            </div>
                            <p className="description-rhh9">{timelineEvent.props.description}</p>
                        </button>
                    )
                })}
            </div>
        </div>
    )
}

export default Timeline

interface TimelineEventProps {
    icon?: string | null | undefined
    title?: string | null | undefined
    description?: string | null | undefined
}

const TimelineEvent: React.FC<TimelineEventProps> = props => null

Timeline.Event = TimelineEvent
