import clsx from "clsx"
import React, {Children, cloneElement} from "react"

interface TabsComponents {
    Tab: React.FC<TabProps>
}

export interface TabsProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "children" | "onSelect"> {
    children?: React.ReactElement | Array<React.ReactElement> | null | undefined
    tabClass?: string | undefined
    tabActiveClass?: string | undefined
    selected: number
    onSelect: (value: number) => void
}

const Tabs: React.FC<TabsProps> & TabsComponents = props => {
    const {onSelect, selected, tabClass, tabActiveClass, className, children, ...otherProps} = props
    const tabs = Children.map(children, (child, index) => {
        const tab = child as React.ReactElement<TabProps>
        return cloneElement(tab, {
            selectedTabClass: clsx(tab.props.selectedTabClass, tabActiveClass),
            onSelect: () => onSelect(index),
            className: clsx(tab.props.className, tabClass),
            selected: index === selected,
        })
    })
    return (
        <div className={className} {...otherProps}>
            {tabs}
        </div>
    )
}

export default Tabs

interface TabProps extends Omit<React.ButtonHTMLAttributes<HTMLButtonElement>, "onSelect"> {
    selecteTabStyle?: React.CSSProperties | undefined
    selectedTabClass?: string | undefined
    selected?: boolean | undefined
    onSelect?: (() => void) | undefined
}

const Tab: React.FC<TabProps> = props => {
    const {className, children, selectedTabClass, selecteTabStyle, style, selected, onSelect, ...otherProps} = props

    return (
        <button
            {...otherProps}
            className={clsx(className, selected ? selectedTabClass : undefined)}
            style={{...style, ...(selected ? selecteTabStyle : undefined)}}
            onClick={onSelect ? () => onSelect() : undefined}
        >
            {children}
        </button>
    )
}

Tabs.Tab = Tab
