import clsx from "clsx"
import {GatsbyImage, IGatsbyImageData} from "gatsby-plugin-image"
import {Children, isValidElement, useState} from "react"
import Fader from "~/components/UI/Fader/Fader"
import RichTextField from "~/components/UI/RichText/RichText"
import Tabs from "~/components/UI/Tabs/Tabs"
import "./Approach2.css"

interface ApproachProps extends React.HTMLAttributes<HTMLDivElement> {}

interface ApproachComponents {
    Card: React.FC<IconCardProps>
}

const Approach2: React.FC<ApproachProps> & ApproachComponents = props => {
    const {className, children, ...otherProps} = props
    const [selectd, setSelected] = useState(0)

    const cards: Array<React.ReactElement<IconCardProps>> = []

    Children.forEach(children, child => {
        if (!child || !isValidElement<IconCardProps>(child)) return
        cards.push(child)
    })

    return (
        <div className={clsx("approach-7pwc", className)} {...otherProps}>
            <Tabs selected={selectd} onSelect={setSelected} className="tabs-7pwc" tabClass="tab-7pwc">
                <Tabs.Tab key="know" className="know-tab-7pwc" selectedTabClass="know-tab-selected-7pwc">
                    {cards[0]!.props.title}
                </Tabs.Tab>
                <Tabs.Tab key="match" className="match-tab-7pwc" selectedTabClass="match-tab-selected-7pwc">
                    {cards[1]!.props.title}
                </Tabs.Tab>
                <Tabs.Tab key="power" className="power-tab-7pwc" selectedTabClass="power-tab-selected-7pwc">
                    {cards[2]!.props.title}
                </Tabs.Tab>
            </Tabs>
            <div className="fader-container-7pwc">
                <div className="fader-border-7pwc" />
                <Fader className="fader-7pwc" selected={selectd} onSelect={setSelected}>
                    {cards.map((card, index) => {
                        return <IconCard key={index} {...card.props} />
                    })}
                </Fader>
            </div>
            <Fader selected={selectd} onSelect={setSelected} className="description-container-7pwc">
                {cards.map((card, index) => {
                    return (
                        <div key={index}>
                            <RichTextField className="chars-p-80">{card.props.description}</RichTextField>
                            <div className="description-children-7pwc">{card.props.children}</div>
                        </div>
                    )
                })}
            </Fader>
        </div>
    )
}

export default Approach2

interface IconCardProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title" | "onSelect"> {
    title: string | null | undefined
    image: IGatsbyImageData | null | undefined
    imageAlt: string | null | undefined
    selected?: number | undefined
    onSelect?: ((value: number) => void) | undefined
    description?: string | null | undefined
    url?: string | null | undefined
}

const IconCard: React.FC<IconCardProps> = props => {
    const {title, image, imageAlt, selected, onSelect, url, children, className, ...otherProps} = props
    return (
        <div className={`${title}-7pwc card-7pwc`} {...otherProps}>
            <div className="icon-container-7pwc">
                <div className="icon-badge-7pwc" />
                {image && imageAlt && (
                    <GatsbyImage image={image} alt={imageAlt} className="gatsby-image-7pwc" imgClassName="image-7pwc" />
                )}
            </div>
            <div className="card-children-7pwc">{children}</div>
        </div>
    )
}

const InputCard: React.FC<IconCardProps> = props => null

Approach2.Card = InputCard
